  padding: 5px;
  box-sizing: border-box;
}

.theia-side-panel .theia-git {
  padding-left: 19px;
}

.theia-git .space-between {
  justify-content: space-between;
}

.theia-scm .scmItem .git-status.new {
  color: var(--theia-gitDecoration-untrackedResourceForeground);
}

